@use '../../global' as *;
@mixin m-shift__card(
  $tab-primary: 0,
  $tab-secondary: 32px,
  $tab-tertiary: 64px,
  $pc-primary: 0,
  $pc-secondary: 40px,
  $pc-tertiary: 80px
) {
  @include mq(tablet) {
    &:nth-child(3n + 1) {
      transform: translateY($tab-primary);
    }
    &:nth-child(3n + 2) {
      transform: translateY($tab-secondary);
    }
    &:nth-child(3n + 3) {
      transform: translateY($tab-tertiary);
    }
  }
  @include mq(pc) {
    &:nth-child(3n + 1) {
      transform: translateY($pc-primary);
    }
    &:nth-child(3n + 2) {
      transform: translateY($pc-secondary);
    }
    &:nth-child(3n + 3) {
      transform: translateY($pc-tertiary);
    }
  }
  @content;
}
