.p-page__strengths--intro {
  $this: &;
  &__container {
    width: min(960px, 100%);
    margin: 0 auto;
    position: relative;
    .p-general__cursive-heading {
      @include general__heading--cursive(
        $font-size: var(--font-hero--md),
        $heading: h1,
        $mt: 0
      );
    }

    #{$this}__content[class*='ark-keep-mt'] {
      position: relative;
      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: -10%;
        width: 100%;
        height: 96%;
        background-image: var(--color-linear-antifish);
        z-index: -2;
      }

      #{$this}__desc {
        @include general__content--left();
        @include mq(tablet) {
          z-index: 2;
        }
      }
      #{$this}__media {
        position: relative;
        top: auto;
        right: auto;
        width: 100%;

        @include mq(tablet) {
          position: absolute;
          top: -8%;
          right: -12%;
          width: 80%;
          margin-top: 0;
        }
        @include mq(pc) {
        }
        //mediaに対する疑似要素
        &::before {
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          background: rgba(255 255 255 /72%);
          position: absolute;
          mix-blend-mode: lighten;
          border-radius: 10px;
        }
      }
    }

