.p-card {
  $this: &;
  &.ark-block-container:not(.has-inner) {
    display: flex;
    flex-direction: row;
    margin-left: calc(12px * -1);
    @include mq(tablet) {
      margin-left: 0;
      flex-direction: column;
    }
    #{$this}__media {
      width: min(125px, 33%);
      margin-left: 12px;
      flex: 1 0 auto;
      position: relative;
      @include mq(tablet) {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        width: 100%;
        margin-left: 0;
        &::before {
          position: absolute;
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          background: rgba(255 255 255 / 64%);
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }
      }
    }
    #{$this}__body {
      margin-top: 0;
      width: min(calc(100% - 24px - 125px), 66%);
      margin-left: 12px;
      flex: 1 1 auto;
      @include mq(tablet) {
        margin-left: 0;
        width: 100%;
        padding: 20px 14px;
        aspect-ratio: 5/3;
        background: var(--ark-color_bg);
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.06),
          0px 2px 24px -10px rgba(0, 0, 0, 0.06),
          0px 4px 32px -16px rgba(0, 0, 0, 0.04);
      }
      //p-card__body直下のparagraph
      &.ark-keep-mt > :not(:first-child) {
        font-size: var(--font-xxs);
        margin-top: 0.5em;
      }
    }

    #{$this}__heading {
      display: flex;
      flex-direction: row;
      align-items: center;

      &--main {
        font-size: var(--font-sm);
        font-weight: 400;
        letter-spacing: 0.1em;
        color: #80999c;
      }
      &--sub {
        display: inline-block;
        padding: 0.4em 0.8em;
        border: 1px solid var(--color-weldon-blue);
        // color: var(--color-weldon-blue);
        background-color: var(--color-weldon-blue);
        color: #ffffff;
        border-radius: 2px;
        font-size: 10px;
        line-height: 1;
        @include mq(tablet) {
          padding: 0.4em 0.8em;
        }
      }
    }
  }
}

